window.onload = function() {

let filter_body = document.getElementsByClassName("body-open");
for (i = 0; i < filter_body.length; i++) {
  filter_body[i].style.maxHeight = filter_body[i].scrollHeight + "px";
}


let acc = document.getElementsByClassName("btn-open");
let i;

for (i = 0; i < acc.length; i++) {
  acc[i].addEventListener("click", function() {
    this.classList.toggle("active");
    var panel = this.nextElementSibling;
    if (panel.style.maxHeight){
      panel.style.maxHeight = null;
    } else {
      panel.style.maxHeight = panel.scrollHeight + "px";
    } 
  });
}



let check = document.getElementsByClassName('check-box');

for (i = 0; i < check.length; i++) {
  check[i].addEventListener('click', function() {
    if(this.checked === true) {
      this.parentNode.style.fontWeight = 500;
    } else {
      this.parentNode.style.fontWeight = 400;
    }
  })
}


let color = document.getElementsByClassName('button__color');

for (i = 0; i < color.length; i++) {
  color[i].addEventListener('click', function () {
    let color_circle = this.querySelector('.circle');
    if (!this.classList.contains("active")) {
      this.classList.add("active");
    }

  })
}

let color_clear = document.getElementsByClassName('close__btn');

for  (i = 0; i < color_clear.length; i++)  {
  color_clear[i].addEventListener('click', function () {
    let color_btn = this.previousElementSibling;
    color_btn.classList.remove("active");
  })
}


let filterBtn = document.querySelector('.filters__text-mb');

filterBtn.addEventListener('click', function() {
  let filtersBody = document.querySelector('.catalog-main__filtres');

  filtersBody.classList.toggle('open-mb');
  document.body.scrollTop = document.documentElement.scrollTop = 0;
})


let reset = document.querySelector('.reset');

reset.addEventListener('click', function() {
  let filtersBody = document.querySelector('.catalog-main__filtres');

  filtersBody.classList.toggle('open-mb');
})




let openBth = document.querySelector('.burger__toggle');

openBth.addEventListener('click', function() {
  let burger = document.querySelector('.menu-mb');

  burger.classList.toggle('open-burger-mb');
})

let close = document.querySelector('.close');

close.addEventListener('click', function() {
  let burger = document.querySelector('.menu-mb');

  burger.classList.toggle('open-burger-mb');
})

}